}
.container {
    display: block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-align: left;
    font-size: 16px;
    line-height: 24px;
    padding: 16px 0 0 24px;
}

.container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
}

.checkmark {
    position: absolute;
    /*top: 18px;*/
    left: 0;
    height: 22px;
    width: 22px;
    background-color: transparent;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    border: solid 1px #007AFF;;
    border-radius: 50%;
}

.container input:checked ~ .checkmark {
    background-color: #007AFF;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.container input:checked ~ .checkmark:after {
    display: block;
}

.container .checkmark:after {
    left: 8px;
    top: 4px;
    width: 5px;
    height: 9px;
    border: solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

